@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
@import url(https://fonts.googleapis.com/css?family=Staatliches);

body {
  margin: 0;
  padding: 0;
  font-family: verdana, Lucida, Lucida;
 
}

.searchContainer {
    background: rgba(244,103,50,1)
}

.searchBox {
    text-align: center;
    padding: 30px 0 0 0;
}

.search {
    text-align: center;
    padding: 15px;
    border-radius: 9px;
    background-color: rgba(0, 0, 0, 0.705);
    border: 1px solid rgba(255, 255, 255, 0.829);
    font-size: large;
    text-transform: capitalize;
    color: #fff;
    transition: all .3s;
}

.search::-webkit-input-placeholder {
    transition: all .3s;
}

.search::-ms-input-placeholder {
    transition: all .3s;
}

.search::placeholder {
    transition: all .3s;
}

.search:focus::-webkit-input-placeholder {
    opacity: 0;
}

.search:focus::-ms-input-placeholder {
    opacity: 0;
}

.search:focus::placeholder {
    opacity: 0;
}
.card {
  position: relative;
  width: 200px;
  font-family: "Montserrat", sans-serif;
  margin: 1rem auto;
  padding: 0.5rem 0 0.4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  /* hyphens: auto; */
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  font-size: 1.2em;
  background-color: #ffffff;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  /* color: rgba(100, 100, 100, 1); */
}

.card:hover {
  -webkit-transform: scale(1.12);
          transform: scale(1.12);
  opacity: 1;
  /*background-color: rgba(231, 228, 228, 0.932);*/
  cursor: pointer;
}

a {
  text-decoration: none;
}

.card__title {
  height: 3rem;
  padding: 0 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.card__image {
  height: 150px;
  width: 100%;
}

.region-details {
  font-family: "Montserrat", sans-serif;
}

h2 {
  font-size: 2em;
  color: white;
}

.h2black {
  color: black;
}

.region-card {
  height: 400px;
  list-style: none;
}

.region-card h3 {
  cursor: pointer;
}

.regiontext {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
}

.detailCard {
  width: 100%;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 200px);
  -webkit-justify-content: center;
          justify-content: center;
  grid-row-gap: 2rem;
  grid-column-gap: 3.5rem;
  padding: 0;
  margin: 1.1em;
}

.regionFlagContainer {
  width: calc(100% - 2rem);
}

.detailsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
}

.header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.075), rgba(0, 0, 0, 0.849)), url(/travel-guide/static/media/BaganMyanmar.3b735d9e.jpg);
    background-position: 20% 85%;

    background-repeat: no-repeat;
    background-size: cover;
    background-origin: content-box;
    /* mix-blend-mode: saturation; */

}

header.header {
    padding-top: 0;
}

@media screen and (min-width: 1300px) {
    .header {
        background-position: 10% 80%;
    }
}

